@import url(https://fonts.googleapis.com/css?family=Montserrat:400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

 body {
  font-size: 16px;
  color: #404040;
  font-family: Montserrat, sans-serif;

  background-image: linear-gradient(to bottom right, #ff9eaa 0% 65%, #e860ff 95% 100%);
  background-image: url(https://journalistsresource.org/wp-content/uploads/2015/04/bitcoin-1813503_1280.jpg);
  background-position: center;
  background-attachment: fixed;
  margin: 0;
  padding: 2rem 0;
  /* display: grid; */
  place-items: center;
  box-sizing: border-box;
}
.card {
  background-color: #fff;
  /* max-width: 360px; */
  /* display: grid !important; */
  /* grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); */
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row !important;
          flex-direction: row !important;
  overflow: hidden;
  border-radius: 2rem;
  box-shadow: 0px 1rem 1.5rem rgba(0,0,0,0.5);
}
.card .banner {
  background-image: url("https://www.usnews.com/dims4/USNEWS/175a9e0/2147483647/crop/2121x1392%2B0%2B2/resize/640x420/quality/85/?url=http%3A%2F%2Fmedia.beam.usnews.com%2Fc7%2Ff2%2Fd298f1c84e9d8ce14f1112318796%2F200901-stock.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 11rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: center;
          justify-content: center;
  box-sizing: border-box;
  
  /* grid-column-start: 1;
  grid-column-end: 4; */

}
.card .banner svg {
  background-color: #fff;
  width: 8rem;
  height: 8rem;
  box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.3);
  border-radius: 50%;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  transition: -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28), -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
.card .banner svg:hover {
  -webkit-transform: translateY(50%) scale(1.3);
          transform: translateY(50%) scale(1.3);
}
.card .menu {
  width: 100%;
  height: 5.5rem;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  position: relative;
  box-sizing: border-box;

/*   
  grid-column-start: 1;
  grid-column-end: 4; */
}
.card .menu .opener {
  width: 2.5rem;
  height: 2.5rem;
  position: relative;
  border-radius: 50%;
  transition: background-color 100ms ease-in-out;
}
.card .menu .opener:hover {
  background-color: #f2f2f2;
}
.card .menu .opener span {
  background-color: #404040;
  width: 0.4rem;
  height: 0.4rem;
  position: absolute;
  top: 0;
  left: calc(50% - 0.2rem);
  border-radius: 50%;
}
.card .menu .opener span:nth-child(1) {
  top: 0.45rem;
}
.card .menu .opener span:nth-child(2) {
  top: 1.05rem;
}
.card .menu .opener span:nth-child(3) {
  top: 1.65rem;
}
.card h2.name {
  text-align: center;
  padding: 0 2rem 0.5rem;
  margin: 0;
}
.card .title {
  color: #a0a0a0;
  font-size: 0.85rem;
  text-align: center;
  padding: 0 2rem 1.2rem;
}
.card .actions {
  padding: 0 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* order: 99; */
}
.card .actions .follow-info {
  /* padding: 0 0 1rem; */
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.card .actions .follow-info h2 {
  text-align: center;
  width: 50%;
  margin: 0;
  box-sizing: border-box;
}
.card .actions .follow-info h2 a {
  text-decoration: none;
  padding: 0.8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 0.8rem;
  transition: background-color 100ms ease-in-out;
}
.card .actions .follow-info h2 a span {
  color: #1c9eff;
  font-weight: bold;
  font-size: 1rem;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform: scaleY(1.3);
          transform: scaleY(1.3);
  transition: color 100ms ease-in-out;
}
.card .actions .follow-info h2 a small {
  color: #afafaf;
  font-size: 0.85rem;
  font-weight: normal;
}
.card .actions .follow-info h2 a:hover {
  background-color: #f2f2f2;
}
.card .actions .follow-info h2 a:hover span {
  color: #007ad6;
}
/* .card .actions  */
.follow-btn button {
  color: inherit;
  font: inherit;
  font-weight: bold;
  background-color: #ffd01a;
  width: 100%;
  border: none;
  padding: 1rem;
  outline: none;
  box-sizing: border-box;
  border-radius: 1.5rem/50%;
  transition: background-color 100ms ease-in-out, -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: background-color 100ms ease-in-out, transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: background-color 100ms ease-in-out, transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28), -webkit-transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
.card .actions .follow-btn button:hover {
  background-color: #efb10a;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.card .actions .follow-btn button:active {
  background-color: #e8a200;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.card .desc {
  /* text-align: justify; */
  padding: 0 2rem 2.5rem;
  /* order: 100; */
}



/* ================================================================================
                                 CARD FLIP STYLES
================================================================================ */




.poster-container {
min-width: 350px;
	min-height: 500px;
  position: relative;

}
.poster {
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all 1s ease-in-out;
  width: 100%;
  display: -webkit-flex;
  display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.flipped .pic{
  display: none;
}
.flipped .pic.back{
  display: block;
}
.poster .pic {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  overflow: hidden;
}
.poster .back {
  background: #eaeaed;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgb(0 0 0 / 50%)), url("https://www.usnews.com/dims4/USNEWS/175a9e0/2147483647/crop/2121x1392%2B0%2B2/resize/640x420/quality/85/?url=http%3A%2F%2Fmedia.beam.usnews.com%2Fc7%2Ff2%2Fd298f1c84e9d8ce14f1112318796%2F200901-stock.jpg");
  color: #0087cc;
  text-align: center;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

